body {
    padding: 50px;
    font: 14px "Lucida Grande",Helvetica,Arial,sans-serif;
}

a {
    color: #00B7FF;
}
.breadcrumbs {
    display: block;
    margin-bottom: 25px;
}

.breadcrumbs__link {
    position: relative;
    margin-right: 30px;
    text-decoration: none;
    color: #00B7FF;
}

.breadcrumbs__link:last-child {
    color: #000000;
}

.breadcrumbs__link:not(:last-child)::after {
    content: '\2192';
    position: absolute;
    right: -20px;
    pointer-events: none;
}
.branches-list {
  list-style: none;
}

.branches-list__item {
  text-decoration: none;
  font-size: 16px;
}
.commits-list {
  list-style: none;
  border-top: 1px solid grey;
}

.commits-list__item {
  margin-bottom: 15px;
  font-size: 14px;
}

.tree__item[href$="file"] {
  color: red;
}
.tree {
  list-style: none;
  margin-top: 2px solid #00B7FF;
}

.tree__item {
  margin-bottom: 5px;
}
